import NewExpense from "./components/NewExpense/NewExpense";
import Expenses from "./components/Expenses/Expenses";
import React, {useContext, useState} from "react";
import MyProvider from "./context/MyProvider";
import MyContext from "./context/MyContext";
import {logDOM} from "@testing-library/react";
function App() {
	const DUMMY_EXPENSES = [
		{
			id: 'e1',
			category: '',
			title: 'Toilet Paper',
			amount: 94.12,
			date: new Date(2020, 7, 14),
		},
		{
			id: 'e2',
			category: '',
			title: 'New TV',
			amount: 799.49,
			date: new Date(2021, 2, 12)
		},
		{
			id: 'e3',
			category: '',
			title: 'Car Insurance',
			amount: 294.67,
			date: new Date(2021, 2, 28),
		},
		{
			id: 'e4',
			category: '',
			title: 'New Desk (Wooden)',
			amount: 450,
			date: new Date(2021, 5, 12),
		},
	];
	const [realData, setRealData] = useState(DUMMY_EXPENSES);
	const [targetId, setTargetId] = useState(); // list     id
	const [year, setYear] = useState('2021')

	//   
	const handleYearChange = (selectedYear) => {
		setYear(selectedYear);
	};

	const addExpenseHandler = (expenseData) => {
			//   
			setRealData((prevExpenses) => [
				...prevExpenses,
				{ ...expenseData, id: Math.random().toString() },
			]);
	};
	const editExpenseHandler = (expense) => {
		//       
		const updatedExpenses = realData.map((exp) =>
			exp.id === targetId ? { ...exp, ...expense } : exp
		);
		setRealData(updatedExpenses);
		console.log("");
	};

	return (
		<MyProvider targetId={targetId} setTargetId={setTargetId} >
			<NewExpense
				onEditExpense={editExpenseHandler}
				onAddExpense={addExpenseHandler}
				items={realData}
				setRealData={setRealData}
				year={year}
				setYear={setYear}
				onYearChange={handleYearChange}
			/>
			<Expenses
				items={realData}
				setRealData={setRealData}
				year={year}
				setYear={setYear}
			/>
		</MyProvider>
	);
}

export default App;
